import React from "react";

function CardSearchLoading(props){
    return(
        <div className={`w-full md:w-[49%] lg:w-[32%] xl:w-[24%] my-3 rounded shadow-lg h-[500px] relative overflow-hidden anim-right ${props.status ? 'hidden' : 'block'}`}>
            
        <h6 className="bg-gray-200 p-2 rounded-full absolute top-4 right-4 text-white w-14 h-9"></h6>
        <div className="absolute bottom-5 left-1 right-1 mx-auto p-2 h-16 rounded bg-gray-200 w-[90%] text-white text-center">
            
        </div>
        </div>
    )
}

export default CardSearchLoading;