import React, {useState} from "react";
import {Link} from 'react-router-dom';

function SerachSearchBox(props){
    let [word, setWord] = useState(props.data);
    let change = (e)=>{
      setWord(e.target.value);
    }
    if(word == undefined){
      setWord("");
    }
    let scroll = ()=>{
      window.scrollTo({
        top: 0,
        behavior: 'smooth'
      
    })
  }
    return(
        <div className="my-8 flex justify-center px-4">
      <div className="w-full max-w-[700px] relative">
        <input type="text" onChange={change} value={word} onKeyDown={(e)=>{if(e.key == 'Enter'){
          document.querySelector("a.search-enter").click();
        }}} name="search" id="search" placeholder="Search..." className="outline-none w-full py-2 px-4 border-2 border-gray-400 rounded-sm text-gray-400 focus:border-gray-500 focus:text-gray-900  transition-all duration-200"/>
        <Link to={`/search/${word}`} onClick={scroll} className="search-enter material-icons absolute right-0 top-0 p-2 text-gray-400 transition-all duration-200">
          search
        </Link>
      </div>
     </div>
    )
}

export default SerachSearchBox;