import React, {useContext, useState, useEffect} from "react";
import CardSearch from "./Serach/CardSearch";
import SerachSearchBox from "./Serach/SearchSerachBox";
import SerachTopBanner from "./Serach/SearchTopBanner";
import {useParams} from 'react-router-dom';
import ProductContext from "./Context/ProductContext";

function SearchPage(props){
  let [count, setcount] = useState(8)
  let [firstload, setfirstload] = useState(false);
  let data = useParams().word;
  let [load, setload] = useState(false);
  let {state, action} = useContext(ProductContext);
  useEffect(()=>{
    document.title = "HM Shop | Search";
    setcount(8);
    setfirstload(true);
    setTimeout(()=>{
      setfirstload(false);
    }, 1000);
  }, [data]);
  let productState = state.product.filter(item=> {
    if(data == null){
      return null;
    }else{
      let word = new RegExp(data, 'i');
    if(item.title.search(word) > -1){
      return item;
    }
    }
  }
    );
    let more = ()=>{
      setload(true);
      setTimeout(() => {
        setload(false);
        setcount(count+ 8);
      }, 1000);
    }
    let product = productState.slice(0,count).map((item, key)=><CardSearch img={item.img} key={key} title={item.title} price={item.price} id={item.id} />);
  

    return (
    <div className="container mx-auto px-4 xl:px-0 flex flex-col">

      <SerachTopBanner />

      <SerachSearchBox data={data} />

      <div className="border-b-[1px] border-gray-300 py-2 text-xl text-gray-700">
        Search Result :
      </div>
      <div className={`w-full text-center ${!firstload ? 'hidden' : 'block'} min-h-[391px]`}>
      <span className={`material-icons animate-spin text-3xl my-12`}>
            sync
            </span>
      </div>
      <div className={`flex flex-wrap flex-col md:flex-row justify-center  xl:px-0 md:justify-between ${firstload ? 'hidden' : 'flex'} min-h-[391px]`}>
        {product}
        {data && product.length == 0 ? <h1 className="text-center block w-full text-gray-500 text-2xl py-12">No results found</h1> : null}
        {!data ? <h1 className="text-center block w-full text-gray-500 text-2xl py-12">Not a word to search ...</h1> : null}
      </div>

      <div className="px-0 w-full flex flex-col">
      <div className={`${load ? "block" : "hidden"} text-center w-full`}>
      <span className={`material-icons animate-spin text-3xl my-8`}>
            sync
            </span>
      </div>
        <div onClick={more} className={`bg-gray-700 hover:bg-gray-800 cursor-pointer w-full text-white text-center p-4 rounded my-4 transition-all duration-200 ${productState.length > count ? 'null': 'hidden'} ${firstload ? 'hidden' : 'block'}`}>
          Load More
        </div>
      </div>

    </div>
    )
}
export default SearchPage;