import React, {useState} from "react";
import {Link} from 'react-router-dom'
import ScrollTopAction from "../Action/ScrollTopAction";
import CardSearchLoading from './CardSearchLoading'
function CardSearch(props){
  let [load, setLoad] = useState(false);
    return (
      <>
      <CardSearchLoading status={load} />

        <div className={`w-full md:w-[49%] lg:w-[32%] xl:w-[24%] my-3 rounded shadow-lg h-[500px] relative overflow-hidden ${load ? 'block' : 'hidden'}`}>
        <Link onClick={ScrollTopAction} to={`/product/${props.id}`}>
        <img src={`./img/${props.img}`} alt="" onLoad={()=>{setLoad(true)}} className="h-full w-full object-cover object-top hover:scale-105 transition-all duration-300 rounded-t"/>
        </Link>
        <h6 className="bg-gray-600 p-2 rounded-full w-fit absolute top-4 right-4 text-white">${props.price}</h6>
        <div className="p-2 absolute bottom-0 left-0 h-fit p-2 bg-gray-900/90 w-full text-white rounded-b text-center bd">
          <h5 className="p-2 overflow-hidden text-ellipsis whitespace-nowrap">{props.title}</h5>
          
            <Link to={`/product/${props.id}`} onClick={ScrollTopAction} className="bg-gray-600 hover:bg-gray-700 p-2 rounded w-full cursor-pointer block transition-all duration-200">Product Detail</Link>
          
        </div>
      </div>
      </>
    )
}

export default CardSearch;